.section  {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    min-height: 40vh;
    flex-direction: column;
    background: #ede7f6;
}
footer{
    position: relative;
    width: 100%;
    height: auto;
    padding: 50px 100px;
    background: #111;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
footer .containers{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;

}

footer .containers .sec{
    margin-right: 30px;

}

footer .containers .sec.aboutus{
    width: 40%;
}

footer .containers h2{
    position: relative;
    color: #fff;
    font-weight: 500;
    margin-bottom: 15px;
}
footer .containers  h2::before{
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 50px;
    height: 2px;
    background: #f00;
}

footer .containers .sec.aboutus p{
    color:#999;
}
.sci{
    margin-top: 20px;
    display: flex;
}
.sci li{
    list-style: none;
}

.sci li a {
    display: inline-block;
    width: 40px;
    height: 40px;
    color: #ede7f6;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    text-decoration: none;
    border-radius: 4px;
}

.sci li a:hover{
    background: #f00;
    color: white;
}

.quickLinks{
    position: relative;
    width: 25%;

    
}

.quickLinks ul li{
    list-style: none;
}

.quickLinks ul li a{
    color: #999;
    text-decoration: none;
    margin-bottom: 10px;
    display: inline-block;
}
.quickLinks ul li a:hover{
    color: #fff;

}

.contact{
    width:calc(35% - 60px);
    margin-right: 0 !important;

}
.contact .info{
    position: relative;
}

.contact .info li{
    display: flex;
    margin-bottom: 16px;

}
.contact .info li span:nth-child(1){
    color: #fff;
    font-size: 20px;
    margin-right: 10px;
}

.contact .info li span{
    color: #999;
}
.contact .info li  a{

    color: #999;
    text-decoration: none;

}
.contact .info li a:hover{
    color: #fff;
}

.copyrightText{
    width: 100%;
    background: #181818;
    text-align: center;
    color: #999;
}


.contmail a{
    font-size: 15px;
}


@media (max-width: 991px) {
    footer{
        padding: 40px;
    }
    footer .containers{
        flex-direction: column;
    }
    footer .containers .sec {
        margin-right: 0;
        margin-bottom: 40px;
    }
    footer .containers .sec.sec.aboutus, .quickLinks, .contact{
        width: 100%;

    }
    .copyrightText{
        padding: 8px 40px;
    }
}
